<template>
<div class="coinFlip" @click="closeForm($event)" >
    <MenuBar/>
    <div class="cgameZone">
        <div class="cgameZoneLeft">
            <div class="cgZLFirst">
                <span class="cgZLFirstTitle">COINFLIP</span>
                <div class="cgZLHeadTails">side
                    <span :style="{opacity:headOpacity}" @click="face='head'"><img src="./img/head.svg"/></span>
                    <span :style="{opacity:tailsOpacity}" @click="face='tails'"><img src="./img/tails.svg"/></span>
                </div>
                <div>
                    <div class="cgZLAmount">
                        <span><img src="./img/coin.svg"/></span>
                        <input v-model="money" type="text" placeholder="Amount..."/>
                    </div>
                    <div>
                        <span @click="money=0">clear</span>
                        <span @click="money+=0.5">+ 0.5</span>
                        <span @click="money+=1">+1</span>
                        <span @click="money+=10">+10</span>
                    </div>
                </div>
                <div class="cgZLDropDOwn">
                    1x
                </div>
                <div @click="createGame" class="cgZLCreate">
                    <span ref="crGame">create game</span>
                </div>
            </div>
            <div class="cgZLMyGame">
                <div class="cgZLMyGameTop">
                    <div class="cgTopMyGame">MY GAME <span style="color:crimson;padding-left:10px"> 0 </span></div>
                    <div>
                        <span @click="histVisible=false" :style="{borderBottom:curBorder}" style="cursor:pointer">Current</span>
                        <span :style="{borderBottom:histBorder}" @click="getCoinflip" style="cursor:pointer">History</span>
                    </div>
                </div>
                <div v-show="textZone && !histVisible">Your Coinflip games will appear here. Start by creating one!</div>
                <CoinFlipZone v-show="!histVisible" name="yourGame" :games='yourGames' @deleteMe='deleteMe'/>
                <CHistZone style="margin-bottom:40px" v-show="histVisible" name="history" :games='history'/>
            </div>
            <div v-show="!histVisible" class="cFZHead">
                <div class="cFZHeadLeft">OPEN GAME<span style="color:crimson; margin-left:10px">6</span></div>
                <div>
                    <div>Price<span style="color:crimson; margin-left:5px">All</span></div>
                    <div>Show games<span style="color:crimson; margin-left:5px">10</span></div>
                    <div>Sort<span style="color:crimson; margin-left:5px">Highest amount first</span></div>
                    <div class="cFZSetting"><img/> Settings</div>
                </div>
            </div>
            <CoinFlipZone ref='coinFlipJoin' v-show="!histVisible" style="margin-bottom:40px" name="joinGame" 
            :games='joinGames' @deleteMe='deleteMe'/>
        </div>
        <div class="cgameZoneRight">
            <ChatBox @signIn='signIn' ref='chatBox' />
        </div>
    </div>
    <Inscription ref='inscription' @accountCreated='accountCreated'/>
    <Login ref='login'/>
    <LLoading ref='loading' :visible='loadingVisible' />
    <LLoading :visible='loading' />
    <ToSteam ref="toSteam"/>
    <YAlert ref="yAlert"/>
</div>

</template>
<script>
</script>
<style src="./coinFlip.css"></style>
<script src="./coinFlip.js"></script>