import MenuBar from '@/components/global/menuBar/MenuBar'
import ChatBox from '@/components/global/chatBox/ChatBox'
import DepositBox from '@/components/global/depositBox/DepositBox'
import Login from '@/components/identification/Login'
import LLoading from '@/components/global/lLoading/LLoading'
import J3Wheels from '@/components/global/j3Wheels/J3Wheels'
import YAlert from '@/components/global/yAlert/YAlert'
import ToSteam from '@/components/global/toSteam/ToSteam'
import Congratulation from '@/components/global/congratulation/Congratulation'
export default {
  name: 'jackpot',
  components: {
    MenuBar,
    ChatBox,
    DepositBox,
    Login,
    LLoading,
    J3Wheels,
    YAlert,
    ToSteam,
    Congratulation
  },
  data () {
    return {
      displayLoading: 'none',
      money: 0,
      showStars: false,
      history: []//[{bet:10,won:1000,date:'2022-07-21 08:37:49'}]
    }
  },
  methods: {
    closeForm(event) {
      let toSteam = this.$refs.toSteam.$refs.firstElement
      if(!toSteam.contains(event.target) && (event.target != this.$refs.crGame)) {
        this.$refs.toSteam.Display = false
      }
      let congrat = this.$refs.congrat.$refs.firstElement
      if(!congrat.contains(event.target)) {
        this.$refs.congrat.Display = false
      }
    },
    signIn () {
      this.$refs.inscription.Display = 'flex'
    },
    accountCreated () {
      this.$refs.login.Display = 'flex'
      this.$refs.login.$refs.yAlertS.display('Account succefully created. Please log in now')
    },
    login () {
      this.$refs.login.Display = 'flex'
    },
    addMoney(a) {
      this.money +=a
      if(a<0) this.money = 0
    },
    play () {
      if(isNaN(this.money)) {
          this.$refs.yAlert.display('not a number')
      }
      else if(this.money<0.1) {
          this.$refs.yAlert.display('you cant bet less than 0.1')
      }
      else if(!this.$store.state.login.connected) {
        this.$refs.toSteam.Display = true
      }
      else if(this.$store.state.userDatas.money < this.money){
        this.$refs.yAlert.display('not Enough money')
      }
      else {
        this.$refs.j3Wheels.commit(this.money) // save the game next launch it
      }
    },
    showingStar: async (k)=>  {
      function waitDelay(t){
          return new Promise((resolve) => {
              setTimeout(resolve, t);
          })
      }
      k.showStars = true
      await waitDelay(2000)
      k.showStars = false
    },
    verdict (result) {
      let i,j = 0
      for(i=0;i<result.length;i++){
        if(result[i]) j++
      }
      let won
      if(j === 0) won = 0
      if(j === 2) won = this.money*(2)
      if(j === 3) won = this.money*(5)
      if(j!==0) this.showingStar(this)
      let multiplier = won == 0 ? 0 : ' (x'+won/this.money+')'
      this.$refs.congrat.display(won+multiplier)
      this.getJackpot()
    },
    getJackpot () {
      const axios = require('axios')
      axios.get(this.$store.state.baseUrl + 'getJackpot.php')
        .then((response) => {
          this.history = response.data
        })
        .catch((error) => {
          console.log(error)
      })
    }
  },
  computed: {
    loadingVisible () {
      return this.$store.state.login.checkSession ? true : false
    }
  },
  mounted () {
    this.getJackpot()
  }
}