import ChatBox from '@/components/global/chatBox/ChatBox'
import CoinFlipZone from '@/components/global/coinFlipZone/CoinFlipZone'
import CHistZone from '@/components/global/coinFlipZone/CHistZone'
import Inscription from '@/components/identification/Inscription'
import Login from '@/components/identification/Login'
import LLoading from '@/components/global/lLoading/LLoading.vue'
import ToSteam from '@/components/global/toSteam/ToSteam'
import YAlert from '@/components/global/yAlert/YAlert'
import MenuBar from '@/components/global/menuBar/MenuBar'
import DepositBox from '@/components/global/depositBox/DepositBox'
export default {
  name: 'coinFlip',
  components: {
    ChatBox,
    CoinFlipZone,
    CHistZone,
    Inscription,
    Login,
    LLoading,
    ToSteam,
    YAlert,
    MenuBar,
    DepositBox
  },
  data () {
    return {
      money: 0,
      yourGames: [],
      joinGames: [{type:'clientSide',id:0,visible:true},
      {type:'clientSide',id:1,visible:true},{type:'clientSide',id:2,visible:true},
      {type:'clientSide',id:3,visible:true},
      {type:'clientSide',id:4,visible:true},{type:'clientSide',id:5,visible:true}],
      face: 'tails',
      histVisible:false,
      loading:false,
      history: []
    }
  },
  methods: {
    closeForm(event) {
      let signUp = this.$refs.inscription.$refs.firstElement
        if(!signUp.contains(event.target) && (event.target != this.$refs.chatBox.$refs.signIn)) {
          this.$refs.inscription.Display = 'none'
        }
      let login = this.$refs.login.$refs.firstElement
        if(!login.contains(event.target) && (event.target != this.$refs.navbar.$refs.login)) {
          this.$refs.login.Display = 'none'
        }
        let toSteam = this.$refs.toSteam.$refs.firstElement
        if(!toSteam.contains(event.target) && (event.target != this.$refs.crGame)) {
          this.$refs.toSteam.Display = false
        }
    },
    signIn () {
      this.$refs.inscription.Display = 'flex'
    },
    accountCreated () {
      this.$refs.login.Display = 'flex'
      this.$refs.login.$refs.yAlertS.display('Account succefully created. Please log in now')
    },
    login () {
      this.$refs.login.Display = 'flex'
    },
    createGame () {
      if(!this.$store.state.login.connected) {
        this.$refs.toSteam.Display = true
      }
      else if(this.$store.state.userDatas.money < this.money){
        this.$refs.yAlert.display('not Enough money')
      }
      else if (this.money < 1) {
        this.$refs.yAlert.display('you can\'t bet less than 1')
      }
      else {
          this.histVisible = false
          this.createYour()
      }
    },
    createYour () {
      if(this.yourGames.length>=6){
        let i,find = false
        for(i=0;i<this.yourGames.length;i++){
          if(this.yourGames[i].visible === false) {
            let k = this
            this.$nextTick(()=>{
              this.$store.commit('loseMoney',this.money)
              k.yourGames[i] = {leftFace:k.face,type:'serverSide',money:k.money,visible:true,id:i}
            })
            find = true
            break
          }
        }
        if(!find) this.$refs.yAlert.display('all slot occupied,please wait')
      }
     else this.yourGames.push({leftFace:this.face,type:'serverSide',money:this.money,visible:true,id:this.yourGames.length})
    },
    deleteMe (playload) {
      playload.games[playload.index].visible = false
      let k = this
      if(playload.games === this.joinGames) { 
        this.$nextTick(()=>{
          k.spawning(playload,k)
        })
      }
    },
    getCoinflip () {
      this.histVisible = true
      this.loading = true
      this.history = []
      const axios = require('axios')
      axios.get(this.$store.state.baseUrl + 'getCoinflip.php')
        .then((response) => {
          this.history = response.data
          console.log(response.data)
          this.loading = false
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
      })
    },
    spawning: async (playload,k)=>  {
      let delays = [1,3,6,5,2,20]
      let d = Math.floor(Math.random()*3)
      function waitDelay(){
          return new Promise((resolve) => {
            setTimeout(resolve, 1000*delays[d]);
          })
      }
      await waitDelay()
      k.$refs.coinFlipJoin.c ++
      playload.games[playload.index].visible = true
      //let c = k.$refs.coinFlipJoin.c
      //k.joinGames.push({type:'clientSide',visible:true,id:c})
      // k.spawning(k)
    }
  },
  computed: {
    loadingVisible () {
      return this.$store.state.login.checkSession ? true : false
    },
    textZone () {
      let i
      for(i=0;i<this.yourGames.length;i++){
        if(this.yourGames[i].visible === true) {
          return false
        }
      }
      return true
    },
    headOpacity () {
      return this.face === 'head' ? 1:0.4
    },
    tailsOpacity () {
      return this.face === 'tails' ? 1:0.4
    },
    histBorder () {
      return this.histVisible ? 'solid 1px #EA2033' : 'none'
    },
    curBorder () {
      return this.histVisible ? 'none' : 'solid 1px #EA2033'
    }
  }
}