import MyProfile from '@/components/global/myProfile/MyProfile'

export default {
    name: 'gameBoard',
    components: {
        MyProfile
    },
    data () {
        return {
            Display: false,
            numbers:[{id:1,items:[]},{id:3,items:[]},
                {id:5,items:[]},{id:10,items:[]},
                {id:20,items:[]}
            ],
            history: [],
            cursor:0
        }
    },
    computed: {
        toWin () {
            let i,j, won = 0
            for(i=0;i<this.numbers.length;i++) {
                let number = this.numbers[i]
                for(j=0;j<number.items.length;j++) {
                    won +=parseFloat(number.items[j].value)*(number.id+1)
                }
            }
            return won
        }
    },
    methods: {
        displaying() {
            this.Display = !this.Display
        },
        dropping (index,event) {
            event.preventDefault()
            this.numbers[index].items.push(this.$refs.scraps.current)
            let numbs = JSON.parse(JSON.stringify(this.numbers))
            this.history = [...this.history.slice(0,this.cursor+1),numbs]
            this.cursor++
        },
        cancel (event) {
            event.preventDefault()
        },
        undo () {
            if(this.cursor>0) {
                this.cursor --
                this.numbers = JSON.parse(JSON.stringify(this.history[this.cursor]))
            }
        },
        redo () {
            if(this.cursor<this.history.length-1) {
                this.cursor ++
                this.numbers = JSON.parse(JSON.stringify(this.history[this.cursor]))
            }
        },
        resetGame () {
            this.toWin = 0
            this.numbers = [{id:1,items:[]},{id:3,items:[]},
                {id:5,items:[]},{id:10,items:[]},
                {id:20,items:[]}
            ]

            // resetting history
            this.history = []
            let numbs = JSON.parse(JSON.stringify(this.numbers))
            this.history.push(numbs)
        },
        verdict (result) {
            let i, number,won = 0
            for(i=0;i<this.numbers.length;i++) {
                if(this.numbers[i].id == result) {
                    number = this.numbers[i]
                    break
                }
            }
            for(i=0;i<number.items.length;i++) {
                won +=parseFloat(number.items[i].value)
            }
            alert('vous avez gagné '+won*(number.id+1))
            this.resetGame()
        }
    },
    mounted () {
        let numbs = JSON.parse(JSON.stringify(this.numbers))
        this.history.push(numbs)
    }
}