<template>
<div v-show="Display" class="gameBoard">
    <div @click="Display=false" class="gameCross"><img src="./img/more.svg"/></div>
    <div class="gameBTitle">
        <div class="gameBTLeft">WHEEL OF FORTUNE <img src="./img/sound.svg"/></div>
        <div><img src="./img/help.svg"/>HELP</div>
    </div>
    <div>
        <img src="./img/information.svg"/>Place your scrap into the betting slot.
        If the wheel lands on you selected number(s) you win!
    </div>
    <div>
        <div class="gameBRule">
            <span>TIME LEFT</span>
            <span>All bet are final once the wheel spins</span>
        </div>
        <div>
            <img src="./img/lock.svg"/>
        </div>
    </div>
    <div class="gameBNumber">
        <div v-for="(numb,index) in numbers"
        :key="index" 
        @drop="dropping(index,$event)"
        @dragenter="cancel"
        @dragover="cancel"
        >
            <img class="gameBNumberImg" :src="require('./img/'+numb.id+'.png')"/>
            <img v-for="(itm,index) in numb.items" :key="index" 
                class="gameBNumberScrap" :src="require('@/assets/scraps/'+itm.image)"
            />
        </div>
    </div>
    <div class="gameBScrap">
        <div class="gameBScrapHead">
            SCRAP STACK<img class="gameBSHelp" src="./img/help.svg"/><img src="./img/setting.svg"/>
        </div>
        <div>
            <MyProfile ref="scraps"/>
        </div>
    </div>
    <div class="gameBMenu">
        <div style="min-width:71px;margin-left:0px" @click="undo"><img src=""/>UNDO</div>
        <div style="min-width:70px" @click="redo"><img src=""/>REDO</div>
        <div style="min-width:22px" @click="resetGame"><img src="./img/trash.svg"/></div>
        <div style="width:100%" class="gameBMenuSpace">to win: {{ toWin }}</div>
        <div style="min-width:85px" @click="this.$emit('spin')">BET</div>
    </div>
    <div>
        #jkdjsdkfddejfef
    </div>
</div>
</template>
<script src='./gameBoard.js'>
</script>
<style src='./gameBoard.css'>
</style>
