import CoinFlipping from '@/components/global/coinFlipping/CoinFlipping'
import ToSteam from '@/components/global/toSteam/ToSteam'
import YAlert from '@/components/global/yAlert/YAlert'

export default {
    name: 'coinFlipZone',
    components: {
        CoinFlipping,
        ToSteam,
        YAlert
    },
    props: {
        games: {
            type: Array
        },
        name: {
            type:String
        }
    },
    data () {
        return {
            c: 3 // counter (incremented whenever the parent push another element)
        }
    },
    methods: {
        closeForm(event) {
            let toSteam = this.$refs.toSteam.$refs.firstElement
            if(!toSteam.contains(event.target) && (event.target != this.$refs.crGame) && event.target.textContent!=='join game') {
              this.$refs.toSteam.Display = false
            }
          },
        notConnected () {this.$refs.toSteam.Display = true},
        notEnough () { this.$refs.yAlert.display('not enough money') },
        deleteMe(id) {
            this.$emit('deleteMe',{index:id,games:this.games})
        }
    }
}