export default {
  name: 'MyProfile',
  props: {
    message: String
  },
  data () {
    return {
      position: 0,
      elements: [{image:'chip.svg', name:"Chip",value:'0.01'},
        {image:'engine.svg',name:'Engine',value:'0.1'},
        {image:'fan.svg',name:'Fan',value:'1'},
        {image:'screw_bolt.svg',name:'Screw and bolt',value:'5'},
        {image:'hammer.svg',name:'hammer',value:'10'},
        {image:'gear.svg',name:'gear',value:'20'}
      ],
      current:[],
      numbRow: []
    }
  },
  methods: {
    move (i) {
      if ((this.position > -(this.numbRow.length-1)*100 && i === -1) || (this.position < 0 && i === 1)) {
        this.position += i * 100
      }
    },
    drag (index) {
      this.current = this.elements[index]
    }
  },
  computed: {
    Position () {
      return this.position + '%'
    }
  },
  mounted () {
    let a = Math.ceil(this.elements.length/5)
    this.numbRow = new Array(a)
  }
}
