import JWheel from './jWheel/JWheel'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
export default {
    name: 'j3Wheels',
    components: {
        JWheel,
        LLoading1
    },
    data () {
        return {
            wheels: ['1','2','3'],
            canPlay: true,
            userDatas: this.$store.state.userDatas,
            loading: false,
            result:[false,false,false]
        }
    },
    methods: {
        plays (w0,w1,w2) {
            if(this.canPlay) {
                this.canPlay = false
                this.$refs.wheel0.reset()
                this.$refs.wheel1.reset()
                this.$refs.wheel2.reset()
                this.play(this,w0,w1,w2)
            }
        },
        play: async (k,w0,w1,w2)=>  {
            function waitDelay(){
                return new Promise((resolve) => {
                    setTimeout(resolve, 2000);
                })
            }
            k.highlight(k.result,k)
            k.$refs.wheel0.rolling(w0)
            await waitDelay()
            k.$refs.wheel1.rolling(w1)
            await waitDelay()
            k.$refs.wheel2.rolling(w2)
        },
        highlight: async (result,k)=>  {
            function waitDelay(t){
                return new Promise((resolve) => {
                    setTimeout(resolve, t);
                })
            }
            await waitDelay(8000)
            if(result[0]) k.$refs.wheel0.highlight()
            if(result[1]) k.$refs.wheel1.highlight()
            if(result[2]) k.$refs.wheel2.highlight()
            k.$store.commit('connected',{userDatas:k.userDatas})
            await waitDelay(300)
            k.$emit('verdict',result)
            k.canPlay = true
        },
        commit (bet) {
            this.loading = true
            //let n = this.$refs['wheel0'].nbrToken
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'jackCommit.php',{
                bet: bet
            })
              .then((response) => {
                  //console.log(response.data)
                  this.loading = false
                  this.result = response.data.result
                  this.userDatas = response.data.userDatas
                  this.plays(response.data.w0,response.data.w1,response.data.w2)
              })
              .catch((error) => {
                this.loading = false
                console.log(error)
            })
        },
    }
}