export default {
    name: 'jWheel',
    data () {
        return {
            tokens: [{img:'coin.svg',class:'jwNotSelected'},
            {img:'crown.svg',class:'jwNotSelected'},
            {img:'head.svg',class:'jwNotSelected'},
            {img:'tails.svg',class:'jwNotSelected'},
            {img:'vegeta.jpg',class:'jwNotSelected'},
            ],
            nbrToken: 0,
            heightToken: 150,
            position:'0px',
            middle: 1,
            Transition: 'bottom 3.5s ease-out'
        }
    },
    methods: {
        complete (l) {
            let i
            for(i=0;i<l;i++){
                this.tokens = this.tokens.concat(this.tokens)
            }
        },
        rolling (c) {
            this.roll(c,this)
        },
        roll: async (c,k)=>  {
            function waitDelay(t){
                return new Promise((resolve) => {
                    setTimeout(resolve, t);
                })
            }
            k.Transition = 'none'
            k.position = '0px'
            await waitDelay(100)
            k.Transition = 'bottom 3.5s ease-out'
            let p = 6*k.nbrToken*150 + c*150
            k.position = -p+'px'
            k.middle = 6*k.nbrToken + c+1
        },
        highlight () {
            this.tokens[this.middle].class = "jwSelected"
        },
        reset () {
            this.tokens[this.middle].class = "jwNotSelected"
        }
    },
    mounted () {
        this.nbrToken = this.tokens.length
        this.complete(7)
    }
}