import InputI0 from '@/components/identification/subComponents/InputI0.vue'
import ButtonI0 from '@/components/identification/subComponents/ButtonI0.vue'
import ButtonI1 from '@/components/identification/subComponents/ButtonI1.vue'
import FooterI from '@/components/identification/subComponents/FooterI.vue'
import YAlert from '@/components/global/yAlert/YAlert'
export default {
  name: 'inscription',
  components: {
    InputI0,
    ButtonI0,
    ButtonI1,
    FooterI,
    YAlert
  },
  data () {
    return {
      Display: 'none'
    }
  },
  methods: {
    displaying () {
      this.Display = this.Display == 'block' ? 'none':'flex'
    },
    submiting (e) {
      e.preventDefault()
      return false
    },
    toNext () {
      if (this.validation()) {
        const axios = require('axios')
        axios.post(this.$store.state.baseUrl + 'inscription.php', {
          username: this.$refs.login.message,
          email: this.$refs.email.message,
          password: this.$refs.password.message
        })
          .then((response) => {
            if(response.data) {
              this.$emit('accountCreated')
              this.displaying()
            }
            else this.$refs.yAlert.display('there is an account with this data')
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },
    validation () {
      let regex1 = /^.{2,22}$/
      let regex2 = /^.{4,22}$/
      if (!regex1.test(this.$refs.login.message)) {
        this.$refs.yAlert.display('login incorrect')
        return false
      }
      if (!regex2.test(this.$refs.password.message)) {
        this.$refs.yAlert.display('password incorrect')
        return false
      }
      if (this.$refs.password.message !== this.$refs.confirmpw.message) {
        this.$refs.yAlert.display('passwords did not match')
        return false
      }
      return true
    }
  }
}

