import LLoading1 from '@/components/global/lLoading1/LLoading1.vue'
import BMenu from './bMenu/BMenu'
export default {
    name: 'menuBar',
    props: {
        selected: {
            type: String,
            default: 'jackpot'
        }
    },
    components : {
        LLoading1,
        BMenu
    },
    computed: {
        jackClass () { return this.selected === 'jackpot' ? 'navButtonSelected' : 'navButton' },
        coinClass () { return this.selected === 'coinflip' ? 'navButtonSelected' : 'navButton' },
        roshClass () { return this.selected === 'roshambo' ? 'navButtonSelected' : 'navButton' }
    },
    data () {
        return {
            DispMenu:false,
            loading: false,
            dispGMenu: false
        }
    },
    methods: {
        toJackpot () {
            this.$router.push('jackpot')
        },
        toCoinflip () {
            this.$router.push('coinflip')
        },
        toRoshambo () {
            this.$router.push('roshambo')
        },
        deconnexion () {
            this.loading = true
            const axios = require('axios')
            axios.get(this.$store.state.baseUrl + 'login.php')
              .then(() => {
                  this.loading = false
              })
              .catch((error) => {
                  this.loading = false
                console.log(error)
            })
        },
        dispMenu () {
            this.$refs.bMenu.displaying()
        }
    }
}