import Konva from 'konva'

export default {
    name: 'roulette',
    data () {
        return {
            numbers: [3,1,5,1,5,3,1,10,1,3,1,5,1,3,1,20,1,3,1,5,1,3,1,10,1],
            stage: null,
            result: 0,
            group: null,
            wheelGroup: null,
            tween: null,
            tween1: null,
            layer1: null,
            resultNumber: null, // the shape of the number,
            isRolling: false
        }
    },
    methods: {
        rolling () {
            if(this.isRolling) return
            else this.isRolling = true

            this.layer1.hide()
            // reinitialize tweens
            if(this.tween !== null) {
                this.tween.reset()
                //this.tween1.reset()
            }

            this.result = Math.floor(Math.random()*25)
            //this.result = 0
            this.tween = new Konva.Tween({
                node: this.wheelGroup,
                duration: 10,
                rotation: 4*360 - this.result*360/25 + 6*360/25,
                easing: Konva.Easings.BounceEaseOut,
                onFinish: () => {
                    this.resultNumber.text(this.numbers[this.result])
                    this.layer1.show()
                    this.$emit('spinEnd',this.numbers[this.result])
                    this.isRolling = false
                }
            })
            this.tween.play()
        }
    },
    mounted () {
        let width = 510
        let height = 510

        this.stage = new Konva.Stage({
        container: 'roulette',
        width: width,
        height: height
        })

        let layer = new Konva.Layer()

        let group = new Konva.Group({
        x: width/2,
        y: height/2,
        offsetX: width/2,
        offsetY: height/2
        })
        let wheelGroup = new Konva.Group({
            x: width/2,
            y: height/2,
            offsetX: width/2,
            offsetY: height/2
        })

        // Integration of the wheel from wheel.png

        let wheel = this.$refs.wheel
        wheel.onload = function () {
          let img = new Konva.Image({
            x: 0,
            y: 0,
            image: wheel,
            width: width,
            height: height
          })
          wheelGroup.add(img)
        }
        this.wheelGroup = wheelGroup
        layer.add(wheelGroup)
        //wheelGroup.cache()
        // wedges 

        /*let i
        let angle = 360/25

        for(i=0;i<25;i++) {
            let color = (i%2 === 1) ? 'transparent': 'transparent'
            let wedge = new Konva.Wedge({
            id: 'wedge'+ this.numbers[i],
            x: this.stage.width() / 2,
            y: this.stage.height() / 2,
            radius: 220,
            angle: angle,
            fill: color,
            stroke: 'white',
            strokeWidth: 3,
            rotation: angle*i + 177.5 - angle
            })
            wedge.shadowForStrokeEnabled(false)
            wedge.hitStrokeWidth(0)
            group.add(wedge)
        }
        */
        // text wedge (number)
        /*
       for(i=0;i<25;i++) {
            let number = new Konva.Text({
                x:width/2 - 205*Math.cos((angle*i + 6.5)*Math.PI/180),
				y:height/2 - 205*Math.sin((angle*i + 6.5)*Math.PI/180),
                text: this.numbers[i],
                fontSize: 22,
                fontFamily: 'Calibri',
                fill: 'white',
                rotation: angle*i
            })
            number.listening(false)
            group.add(number)
        }
        */
            
        this.group = group
        //layer.add(this.group)

        // an inner cicle with the result on it

        this.layer1 = new Konva.Layer()


        this.resultNumber = new Konva.Text({
            x:width/2-120,
            y:height/2-120,
            width:240,
            height:240,
            align:'center', 
            verticalAlign: 'middle',
            text: 22,
            fontSize: 120,
            fontFamily: 'Calibri',
            fill: 'white'
        })
        this.layer1.add(this.resultNumber)

        let circle5 = new Konva.Circle({
            x: width/2,
            y: height/2,
            radius: 120,
            stroke: 'gold',
            strokeWidth: 3,
            fillLinearGradientStartPoint: { x: width/2, y: -50},
            fillLinearGradientEndPoint: { x: width/2, y: +150},
            fillLinearGradientColorStops: [0, 'rgba(0,0,0,0.9)', 1, 'rgba(0,0,0,0)'],
        })
        this.layer1.add(circle5)    
        this.layer1.hide()
               
        this.stage.add(layer)
        this.stage.add(this.layer1)
        //this.group.cache()
        layer.listening(false)
    }
}
