import InputSearch from '@/components/global/inputSearch/InputSearch'
import { getPlayer } from '@/plugins/getPlayer.js'
export default {
  name: 'chatBox',
  components: {
    InputSearch
  },
  data () {
    return {
      datas: [],
      message:''
    }
  },
  computed: {
    DispSignIn () {
        return this.$store.state.login.connected ? 'none' : 'block'
    },
    DispInput () {
        return this.$store.state.login.connected ? 'block' : 'none'
    }
  },
  methods: {
    getColor () {
      let result = Math.floor(Math.random()*3)
      let colors = ['#D6AC22','#3A8EC7']
      return colors[result]
    },
    sendMessage () {
      if(this.$store.state.userDatas.banned == 1) {
        alert('you were banned from chat !')
        return
      }
      const axios = require('axios')
        axios.post(this.$store.state.baseUrl + 'sendChatMessage.php', {
          message: this.message
        })
        .then(() => {
          this.getMessage()
        })
        .catch((error) => {
          console.log(error)
        })
    },
    getMessage () {
      const axios = require('axios')
        axios.get(this.$store.state.baseUrl + 'getChatMessage.php')
        .then((response) => {
          console.log(response.data)
          this.datas.unshift(response.data[0])
        })
        .catch((error) => {
          console.log(error)
        })
    },
    getCom () {
      let c = this.$store.state.comments
      let p, max = c.length
      p = Math.floor(Math.random()*max)
      return c[p]
    },
    newMessage () {
      let message = this.getCom()
      let date = new Date()
      let player = getPlayer(this.$store.state.players,'')
      let username = player.name
      let image = this.$store.state.baseUrl + 'images/'+ player.image
      this.datas.unshift({date:date,username:username,message:message,image:image})
    },
    spawning: async (k)=>  {
      let delays = [1,3,6,5,2,20]
      let d = Math.floor(Math.random()*delays.length)
      function waitDelay(){
          return new Promise((resolve) => {
            setTimeout(resolve, 1000*delays[d]);
          })
      }
      await waitDelay()
      k.newMessage()
      k.spawning(k)
    }
  },
  mounted () {
    // lets begin with 100 messages -----------------
    let i = 20
    while(i>0) {
      this.newMessage()
      i--
    }
    this.spawning(this)
  }
}