export default {
    name: 'bMenu',
    data () {
        return {
            Display:false
        }
    },
    methods: {
        displaying () {
            this.Display = !this.Display
        },
        loginOut () {
            this.loading = true
            const axios = require('axios')
            axios.get(this.$store.state.baseUrl + 'deconnexion.php')
              .then(() => {
                  this.loading = false
                  this.$router.go('/')
              })
              .catch((error) => {
                  this.loading = false
                console.log(error)
            })
        },
    }
}   