  import { createApp } from 'vue'
  import App from './App.vue'
  import router from './router'
  import { createStore } from 'vuex'
  import middleware from '@grafikri/vue-middleware'
  import { facebookDate } from './plugins/facebookDate'
  import { players } from './plugins/players.js'
  import { comments } from './plugins/comment.js'
  
  const app = createApp(App)
  // Create a new store instance.
  const store = createStore({
      state () {
        return {
          count: 0,
          userDatas: [],
          login: {
            connected: false,
            checkSession: false,
            money: null,
            id: 0
          },
          prices: null,
          players: players,
          comments: comments,    
          baseUrl: 'http://localhost/projet/blw_game_backend/',
          //baseUrl1: 'http://blwcasino.epizy.com/',
          //baseUrl: 'http://blwcasino.epizy.com/blw_game_backend/',
          //baseUrl1: 'https://dembele7.000webhostapp.com/',
          //baseUrl: 'https://dembele7.000webhostapp.com/blw_game_backend/',
          //baseUrl1: 'https://rustvegas.gg/',
          //baseUrl: 'https://rustvegas.gg/blw_game_backend/',
        }
      },
      mutations: {
        increment (state) {
          state.count++
        },
        connected (state,playload) {
          state.login.connected = true
          state.userDatas = playload.userDatas
        },
        loseMoney(state,playload){
          state.userDatas.money -=playload
        },
        checkingSession (state,playload) {
          state.login.checkSession = playload
        },
        setPrices (state,playload) {
          state.prices = playload
        }
      }
    })
  
    app.mixin({
      methods: {
        facebookDate: facebookDate
      }
    })
    app.use(router)
    app.use(store)
    app.mount('#app')
    router.beforeEach(middleware({store}))
  
    
  
    
    

  
