export default {
    name: 'coinFlipping',
    props: {
        parentId:0,
        money:{
            type:Number,
            default:10.00
        },
        type: {
            type:String,
            default:'clientSide' /* game createdd by a bot */
        },
        leftFace: {
            type:String,
            default:'tails'
        },
        old: {
            type: Object
        }
    },
    data () {
        return {
            leftHolder: 'none',
            rightHolder: 'none',
            chrono: 5,
            context: null,
            leftDisplay: true,
            rightDisplay: false,
            headImg: [],
            leftPlayer: {face:'head', lose: false, bet: this.money,name:'Messi',},
            rightPlayer: {face:'tails', lose: false, bet: this.money,name:'Dembélé'},
            face:''
        }
    },
    computed: {
        DisplayJoin () {
            return this.rightDisplay === 'none' ? 'block':'none'
        },
        leftOpacity () {
            return this.leftPlayer.lose ? '0.5':'1'
        },
        rightOpacity () {
            return this.rightPlayer.lose ? '0.5':'1'
        },
        leftAmount () {
            return this.leftPlayer.lose ? 'red':'white'
        },
        rightAmount () {
            return this.rightPlayer.lose ? 'red':'white'
        },
        bxShadow () {
            return this.leftHolder === 'user' || this.rightHolder === 'user' ? 'rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, #EA2033 0px 2px 16px 0px' : 'none'
        },
        joinText () {
            return this.type === 'serverSide' ? 'Waiting player...' : 'join game'
        },
    },
    methods: {
        verdict () {
            if(this.face !== this.leftPlayer.face ) {
                this.leftPlayer.lose = true
                this.leftPlayer.bet = 0.0
                this.rightPlayer.bet *=2
                if(this.leftHolder === 'user' || this.rightHolder === 'user') this.commit()
                else this.selfDestroying(this)
            }
            else {
                this.rightPlayer.lose = true
                this.rightPlayer.bet = 0.0
                this.leftPlayer.bet *=2
                if(this.leftHolder === 'user' || this.rightHolder === 'user') this.commit()
                else this.selfDestroying(this)
            }
        },
        joinGame () {
            if(!this.$store.state.login.connected) {
                this.$emit('notConnected')
            }
            else if(this.$store.state.userDatas.money < this.money) {
                this.$emit('notEnough')
            }
            else {
                this.$store.commit('loseMoney',this.money)
                if(this.type === 'clientSide' && this.rightHolder !== 'bot') {
                    this.rightHolder = 'user'
                    this.rightPlayer.name = this.$store.state.userDatas.username
                }
                this.startGame()
            }
        },
        startGame () {
            this.rightDisplay = true
            this.leftDisplay = true
            document.getElementById(this.parentId+'canvasCoinFlip').style.width = '750px'
           let nIntervId = setInterval(() => {
                if(this.chrono > 0) this.chrono --
                else {
                    clearInterval(nIntervId)
                    this.flipping()
                }
            }, 1000)
        },
        flipping () {
            let aa = Math.floor(Math.random()*2)
            let faces = ['head','tails']
            this.face = faces[aa]
            let j = 0
            let k = this
            function f(){setTimeout(function(){
                k.context.clearRect(0,0,650,650)
                k.context.drawImage(document.getElementById(k.parentId+k.face+k.headImg[j]),-40,-40)
                j++;
                if(j>47){
                    j = 0
                    k.verdict()
                }
                else f()
            }, 1000/24)
            }
            f()
        },
        selfDestroying: async (k)=>  {
            function waitDelay(){
                return new Promise((resolve) => {
                  setTimeout(resolve, 4000)
                })
            }
            await waitDelay()
            // after delaying now destroy !
            k.$emit('deleteMe')
        },
        commit () {
            let userSide = this.leftHolder === 'user' ? 'left' :'right'
            let state = this.leftHolder === 'user' ? this.leftPlayer.lose :this.rightPlayer.lose
            let userFace = this.rightHolder === 'user' ? this.rightPlayer.face:this.leftPlayer.face
            state = state ? 'lose' : 'won'
            let oponent = this.leftHolder === 'bot' ? this.leftPlayer.name :this.rightPlayer.name
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'coinflipCommit.php',{
                userSide: userSide,
                state:state,
                userFace: userFace,
                oponent: oponent,
                money: this.money
            })
              .then((response) => {
                  this.$store.commit('connected',{userDatas:response.data})
                  this.selfDestroying(this)
              })
              .catch((error) => {
                console.log(error)
            })
        },
    },
    async mounted () {
       function init_head(){
            let j
            let img = []
            //initialisation
                for(j=0;j<=48;j++){
                    if(j<10){
                        img.push("000"+j)
                    }
                    else {img.push("00"+j)}
                }
            return img;
        }
        this.headImg = init_head()

        let canvas= document.getElementById(this.parentId+'canvasCoinFlip')
        document.getElementById(this.parentId+'canvasCoinFlip').style.width = '0px'
        this.context = canvas.getContext('2d')

        //----------- initialialisation by type ---------------------------
        if(this.type === 'clientSide') {
            let faces = ['head','tails']
            let f = Math.floor(Math.random()*2)
            this.leftPlayer.face = faces[f]
            this.rightPlayer.face = faces[f] === 'head' ? 'tails' : 'head'
            this.leftHolder = 'bot'
        }
        else if (this.type === 'serverSide') { // created by the user
            let rihgtFace = this.leftFace === 'tails' ? 'head' : 'tails'
            this.leftHolder = 'user'
            this.leftPlayer = {face:this.leftFace, lose: false, bet: this.money,name: this.$store.state.userDatas.username,}
            this.rightPlayer = {face:rihgtFace, lose: false, bet: this.money,name:'Dembélé'}
            this.leftDisplay = true
            this.rightDisplay = false
        }
        else if(this.type === 'history') {
            this.rightDisplay = true
            this.leftDisplay = true
            let usrState = this.old.state === 'won' ? false : true
            let usrMoney = this.old.state === 'won' ? 2*this.old.money : 0
            let user = {face:this.old.userFace, lose: usrState, bet: usrMoney,name:this.$store.state.userDatas.username}
            let opFace = this.old.userFace === 'tails' ? 'head' : 'tails'
            
            let opMoney = this.old.state === 'won' ? 0 : 2*this.old.money
            let oponent = {face:opFace, lose:!usrState,bet:opMoney,name:this.old.oponent}
            
            this.leftPlayer = this.old.userSide === 'left' ? user : oponent
            this.rightPlayer = this.old.userSide === 'right' ? user : oponent
        }
        
        // waiting sometime, if the user did not took it, bots are going to play against each other
            let delays = [2000,4000,7000]
            let d = Math.floor(Math.random()*3)
            function waitDelay(){
                return new Promise((resolve) => {
                    setTimeout(resolve, delays[d]);
                })
            }
            await waitDelay()
            if(this.type === 'clientSide' && this.rightHolder !== 'user') {
                this.rightHolder = 'bot'
                this.startGame()
            }
            if(this.type === 'serverSide' && this.rightHolder !== 'user') {
                this.rightHolder = 'bot'
                this.startGame()
            }
    }
}